// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.

$btn-padding-y:               19px;
$btn-padding-x:               40px;
$btn-font-family:             $input-btn-font-family;
$btn-font-size:               1rem;
$btn-line-height:             1.4;
$btn-white-space:             null;

//$btn-padding-y-sm:            $input-btn-padding-y-sm !default;
//$btn-padding-x-sm:            $input-btn-padding-x-sm !default;
//$btn-font-size-sm:            $input-btn-font-size-sm !default;
//$btn-line-height-sm:          $input-btn-line-height-sm !default;

$btn-padding-y-lg:            19px;
$btn-padding-x-lg:            60px;
//$btn-font-size-lg:            $input-btn-font-size-lg !default;
//$btn-line-height-lg:          $input-btn-line-height-lg !default;

$btn-border-width:            0;

$btn-font-weight:             $font-weight-bold;
$btn-box-shadow:              none;
//$btn-focus-width:             $input-btn-focus-width !default;
$btn-focus-box-shadow:        $input-btn-focus-box-shadow !default;
$btn-disabled-opacity:        .65 !default;
$btn-active-box-shadow:       none;

//$btn-link-disabled-color:     $gray-600 !default;

$btn-block-spacing-y:         .5rem !default;

// Allows for customizing button radius independently from global border radius
$btn-border-radius:           0;
//$btn-border-radius-lg:        $border-radius-lg !default;
//$btn-border-radius-sm:        $border-radius-sm !default;

$btn-transition:              color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out !default;


// Breadcrumbs

$breadcrumb-font-size:              15px;

$breadcrumb-padding-y:              1.0625rem;
$breadcrumb-padding-x:              0;
$breadcrumb-item-padding:           1.3125rem;

$breadcrumb-margin-bottom:          1.4375rem !default;

$breadcrumb-bg:                     transparent;
$breadcrumb-divider-color:          $white;
$breadcrumb-active-color:           $white;
$breadcrumb-divider:                url('data:image/svg+xml,<svg width="4" height="4" viewBox="0 0 4 4" fill="none" xmlns="http://www.w3.org/2000/svg"><circle cx="2" cy="2" r="2" fill="#{$white}"/></svg>');


$breadcrumb-border-radius:          0;
