body {
  overflow-x: hidden;
  a {
    text-decoration: none;
    color: $primary;
    transition: all .5s;
    &:hover {
      text-decoration: none;
    }
  }
  &.nav-show {
    overflow: hidden;
    .page-header {
      .header-menu-list {
        opacity: 0;
        visibility: hidden;
      }
    }
    .page-nav {
      opacity: 1;
      pointer-events: auto;
      transform: translateY(0);
    }
    .page-nav-overlay {
      z-index: 997;
      opacity: 1;
      visibility: visible;
    }
  }

  &.fixed {
    overflow: hidden;
  }
}
// Main
.hide-btn {
  background: none;
  border: none;
  padding: 0;
}
.svg-icon {
  width: 24px;
  height: 24px;
}
.section-title {
  font-family: 'PF DinDisplay Pro', serif;
  font-weight: normal;
  font-size: 70px;
  line-height: 110%;
  @media (max-width: 1689px) {
    font-size: 50px;
  }
  @media (max-width: 768px) {
    font-size: 32px;
  }
}
.main-row {
  margin-left: -18px;
  margin-right: -18px;
  & > .col,
  & > .main-col,
  & > [class*="col-"] {
    padding-left: 18px;
    padding-right: 18px;
  }
  @media (max-width: 1689px) {
    margin-left: -12.5px;
    margin-right: -12.5px;
    & > .col,
    & > .main-col,
    & > [class*="col-"] {
      padding-left: 12.5px;
      padding-right: 12.5px;
    }
  }
}
.img-cover {
  @include img-cover;
}
.btn {
  display: inline-flex;
  justify-content: center;
  border-width: 1px;
  font-family: 'PF DinDisplay Pro', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  @media (max-width: 1689px) {
    padding: 13px 24px;
    font-size: 12px;
  }
}
.btn-primary {
  background-color: $primary;
  color: $black;
  &:hover {
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.25), rgba(255, 255, 255, 0.25)), #FFC700;
    //background-color: #FFC700;
    border-color: #FFC700;
  }
}
.btn-white {
  background-color: $white;
  border: 1px solid transparent;
  color: $black;
  &:hover {
    border-color: $black;
  }
}

.download-presentation-btn {
  position: relative;
  border: none;
  img {
    width: 59px;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
  }
  span {
    padding-left: 59px;
  }
}
.more-box {
  .more-btn {}
}

.form-control {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid $primary;
  border-radius: 0;
  &:focus {
    background-color: transparent;
  }
  &.text-white {
    &::placeholder {
      color: $white;
    }
  }
  @media (max-width: 1689px) {
    height: 42px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 15px;

  }
}
.breadcrumb {
  .breadcrumb-item {
    a {
      color: $white;
      text-decoration-line: underline;
      &:hover {
        color: $primary;
      }
    }
    & + .breadcrumb-item {
      &::before {
        margin-top: -2px;
      }
    }
  }
  @media (max-width: 1689px) {
    padding-top: 12px;
    padding-bottom: 12px;
    margin-bottom: 14px;
    font-size: 12px;
  }
}

// Pages
.page-header {
  width: 100%;
  padding: 21px 0;
  min-height: 50px;
  background-color: transparent;
  border-bottom: 1px solid $white;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  transition: all .3s;
  &.fixed {
    //padding: 30px 0;
    background-color: rgba(25, 29, 36, .9);
    top: -100px;
  }
  &.up {
    background-color: rgba(25, 29, 36, .9);
  }
  &.is-home-page {
    border-bottom-color: transparent;
  }
  &.no-transition {
    transition: none;
  }
  .header-box {
    .logo {
      height: 52px;
      img {
        height: 100%;
      }
    }
  }
  .menu-toggle-btn {
    width: 52px;
    height: 52px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $primary;
    border-radius: 50%;
    color: $black;
    .burger-item {
      width: 22px;
      height: 3px;
      background-color: $black;
      &:not(:last-child) {
        margin-bottom: 5px;
      }
      &:first-child {}
      &:nth-child(2) {}
      &:last-child {}
    }
  }
  .header-menu-list {
    @include lists;
    margin-left: -4px;
    margin-right: -4px;
    padding-left: 14px;
    transition: opacity .3s;
    .header-menu-item {
      padding: 0 4px;
      &.active {
        a {
          background-color: $white;
          color: $black;
          span::after {
            opacity: 0;
          }
        }
      }
      a {
        display: block;
        padding: 9px 17px;
        color: $white;
        font-family: 'PF DinDisplay Pro', sans-serif;
        font-size: 18px;
        line-height: 140%;
        //text-decoration: underline;
        span {
          position: relative;
          &::after {
            content: '';
            width: 100%;
            height: 1px;
            background-color: currentColor;
            position: absolute;
            left: 0;
            bottom: 1px;
            transition: opacity .3s;
          }
        }
        &:hover {
          background-color: $white;
          color: $black;
          span::after {
            opacity: 0;
          }
        }
      }
    }
  }
  .header-map-btn {
    display: inline-flex;
    align-items: center;
    margin-left: 25px;
    padding: 15px 22px;
    color: $white;
    border: 1px solid $white;
    font-size: 18px;
    line-height: 140%;
    .icon {
      width: 15px;
      height: 20px;
    }
  }
  .header-phone-btn {
    display: inline-flex;
    align-items: center;
    padding: 15px 7px;
    margin-left: 25px;
    color: $white;
    font-size: 18px;
    line-height: 140%;
    .icon {
      width: 20px;
      height: 20px;
    }
  }
  @media (max-width: 1689px) {
    .menu-toggle-btn {
      width: 37px;
      height: 37px;
      .burger-item {
        width: 16.65px;
        height: 2.13px;
        &:not(:last-child) {
          margin-bottom: 3.56px;
        }
      }
    }
    .header-menu-list {
      .header-menu-item {
        a {
          padding: 7px 10px;
          font-size: 13px;
        }
      }
    }
    .header-map-btn {
      margin-left: 15px;
      padding: 10px 12px;
      font-size: 13px;
      .icon {
        width: 10px;
        height: 15px;
      }
    }
    .header-phone-btn {
      padding: 10px 3px;
      margin-left: 15px;
      font-size: 13px;
      .icon {
        width: 14px;
        height: 14px;
      }
    }
  }
  @media (max-width: 1200px) {
    .header-menu-list {
      display: none;
    }
  }
  @media (max-width: 768px) {
    padding: 14px 5px;
    background-color: $secondary;
    border-bottom-color: transparent;
    .header-box {
      flex-direction: column;
      .logo-box {
        width: 100%;
        justify-content: space-between;
        margin-bottom: 14px;
      }
      .logo {
        height: 46px;
      }
      .header-content {
        width: 100%;
      }
    }
    .header-map-btn {
      margin-left: 0;
      font-size: 12px;
      .icon {
        width: 7px;
        height: 12px;
      }
    }
    .header-phone-btn {
      padding: 10px 1px;
      margin-left: 11px;
      font-size: 12px;
      .icon {
        width: 12px;
        height: 12px;
      }
    }
  }
}
.page-nav {
  width: 100%;
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 10;
  pointer-events: none;
  opacity: 0;
  transform-origin: 50% 0;
  transform: translateY(-30px);
  transition: all .3s, opacity .15s ease-out;
  &::before {
    content: '';
    width: 100%;
    height: 100%;
    background-color: $primary;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 998;
  }
  & > .container {
    position: relative;
    z-index: 999;
  }
  .nav-box {
    padding: 47px 0 0 0;
  }
  .nav-menu-list {
    @include lists;
    flex-wrap: wrap;
    .nav-menu-item {
      flex: 0 0 25%;
      margin-bottom: 39px;
      font-family: 'PF DinDisplay Pro', sans-serif;
      font-size: 40px;
      line-height: 120%;
      a {
        color: $black;
        &:hover {
          color: $white;
        }
      }
    }
  }
  @media (max-width: 1689px) {
    .nav-box {
      padding: 15px 0 0 0;
    }
    .nav-menu-list {
      .nav-menu-item {
        margin-bottom: 15px;
        font-size: 21px;
      }
    }
  }
  @media (max-width: 768px) {
    .nav-box {
      padding: 25px 0 0 0;
    }
    .nav-menu-list {
      .nav-menu-item {
        flex: 0 0 50%;
        margin-bottom: 25px;
        font-size: 17px;
        a {
          text-decoration-line: underline;
        }
      }
    }
  }
}
.page-nav-overlay {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
}

.full-page-content {
  @media (max-width: 1200px) {
    .section {
      min-height: 100vh;
    }
    .infrastructure-map-section {
      .infrastructure-map-section-inner, .infrastructure-map-box, .map-block {
        min-height: 100vh;
      }
    }
    .contact-map-section {
      .contact-map-section-inner, .contact-map-box, .map-block {
        min-height: 100vh;
      }
    }
  }
}
.section {
  @media (max-width: 768px) {
    padding-left: 5px;
    padding-right: 5px;
  }
}
.first-section {
  padding-top: 100px;
  @media (max-width: 1689px) {
    padding-top: 95px;
  }
  @media (max-width: 768px) {
    padding-top: 137px;
  }
}
.full-section {
  min-height: 100vh;
  position: relative;
  height: 100vh;
  @media (min-width: 1200px) {}
}
.home-section-height {
  @media (min-width: 1440px) {
    min-height: $home-section-height-xxl;
  }
  @media (min-width: 1200px) {
    min-height: $home-section-height-xl;
  }
}
.center-section-inner {
  height: 100%;
  display: flex;
  align-items: center;
}
.first-overlay-section {
  background-position: top center;
  position: relative;
  color: $white;
  &::after {
    content: '';
    width: 100%;
    height: 100%;
    background-color: rgba(25, 29, 36, .9);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }
  & > div {
    position: relative;
    z-index: 10;
  }
  .section-title {
    padding-bottom: 32px;
    margin-bottom: 0;
    font-weight: 400;
  }
  @media (max-width: 1689px) {
    .section-title {
      padding-bottom: 24px;
    }
  }
  @media (max-width: 768px) {
    background-image: none !important;
    background-color: $white;
    color: $black;
    &::after {
      display: none;
    }
    .breadcrumb {
      display: none;
    }
    .section-title {
      padding-bottom: 18px;
      & + p {
        display: none;
      }
    }
  }
}

.home-slider-section {
  .home-carousel {
    .owl-item {
      min-height: 100%;
      height: 100%;
    }
  }
  .home-carousel-item {
    width: 100%;
    background-size: cover;
    background-position: center;
    position: relative;
    &::after {
      content: '';
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, #000 0%, rgba(0, 0, 0, 0) 20.34%);
      opacity: 0.25;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
    }
    .home-carousel-item-wrap {
      padding-top: 188px;
      font-family: 'PF DinDisplay Pro', sans-serif;
      position: relative;
      z-index: 30;
      .home-carousel-item-inner {
        .title-box {
          line-height: 100%;
          p {
            font-size: 20px;
            margin-bottom: 0;
          }
          h1 {
            font-size: 70px;
            font-weight: 400;
            margin-bottom: 13px;
          }
        }
        .btn {
          min-width: 175px;
          border-radius: 0;
          text-transform: uppercase;
          justify-content: center;
          margin-bottom: 32px;
        }
        .slide-text {
          max-width: 419px;
          font-size: 18px;
          line-height: 140%;
        }
      }
      .carousel-item-link {
        font-size: 25px;
        line-height: 140%;
        color: $white;
        text-decoration: underline;
        &:hover {
          color: $primary;
        }
      }
    }
  }
  .home-slider-container {
    position: relative;
    .home-slider-fix-box {
      width: 100%;
      position: absolute;
      right: 0;
      bottom: 25px;
      z-index: 15;
      .home-slider-fix-scrolling {
        .carousel-scroll-btn {
          font-family: 'PF DinDisplay Pro', sans-serif;
          font-weight: bold;
          font-size: 16px;
          line-height: 100%;
          color: $white;
          margin-bottom: 28px;
          span {
            margin-left: 13px;
            text-decoration-line: underline;
          }
        }
      }
      .home-slider-fix-box-right {
        padding: 43px 110px;
        background-color: rgba(255, 199, 0, .75);
      }
      p {
        font-family: 'PF DinDisplay Pro', sans-serif;
        font-size: 40px;
        line-height: 140%;
        color: $black;
      }
    }
  }
  @media (max-width: 1689px) {
    .home-carousel-item {
      .home-carousel-item-wrap {
        padding-top: 158px;
        .home-carousel-item-inner {
          .title-box {
            p {
              font-size: 18px;
            }
            h1 {
              font-size: 50px;
              margin-bottom: 8px;
            }
          }
          .btn {
            min-width: 124px;
            margin-bottom: 22px;
          }
          .slide-text {
            max-width: 324px;
            font-size: 15px;
          }
        }
        .carousel-item-link {
          font-size: 18px;
        }
      }
    }
    .home-slider-container {
      .home-slider-fix-box {
        .home-slider-fix-box-right {
          padding: 30px 77px;
        }
        p {
          font-size: 28px;
        }
      }
    }
  }
  @media (max-width: 1200px) {
    .home-carousel-item {
      .home-carousel-item-wrap {
        height: 100vh;
        padding-top: 158px;
      }
    }
  }
  @media (max-width: 768px) {
    .home-carousel-item .home-carousel-item-wrap .home-carousel-item-inner .title-box h1 {
      font-size: 25px;
    }
    .home-carousel {
      width: calc(100% + 30px);
      margin-left: -15px;
      margin-right: -15px;
      .home-carousel-item-inner {
        padding-left: 15px;
        padding-right: 15px;
      }
    }
    .home-slider-fix-box {
      width: calc(100% + 30px) !important;
      margin-left: -15px;
      margin-right: -15px;
      .home-slider-fix-box-right {
        padding: 26px 22px !important;
        p {
          font-size: 22px;
        }
      }
    }
  }
}
