      }
      .apartment-info-list {
        padding-left: 30px;
        .apartment-info-item {
          &:not(:last-child) {
            margin-bottom: 17px;
          }
          font-size: 14px;
        }
      }
    }
  }
  @media (max-width: 991px) {
    .apartment-block {
      .apartment-title-box {
        margin-bottom: 19px;
        .apartment-title-item {
          &:last-child {
            margin-left: 30px;
          }
        }
        .apartment-title {
          font-size: 32px;
        }
        .apartment-sub-title {
          font-size: 14px;
        }
      }
      .apartment-info-list {
        padding-left: 0;
        .apartment-info-item {
          &:not(:last-child) {
            margin-bottom: 30px;
          }
          &:last-child {
            &::before {
              display: none;
            }
          }
          &::before {
            content: '';
            width: 114px;
            height: 1px;
            background-color: $primary;
            left: calc(50% - 57px);
            top: inherit;
            bottom: -15px;
          }
        }
      }
    }
    .apartment-vector {
      margin-bottom: 30px;
    }
  }
  @media (max-width: 576px) {
    .apartment-vector {
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
}

