      .product-card-title {
        font-size: 14px;
        padding-bottom: 9px;
        margin-bottom: 8px;
      }
      .product-card-text {
        font-size: 12px;
      }
    }
  }
}
.tenants-card-item {
  .product-card-image {
    @include center-box;
    height: 180px;
    img {
      max-width: 70%;
      max-height: 70%;
      object-fit: cover;
    }
  }
  @media (max-width: 1689px) {
    .product-card-image {
      height: 128px;
    }
  }
}

.contact-form {
  font-family: 'PF DinDisplay Pro', sans-serif;
  .btn-box {
    .privacy-box {
      margin-left: 31px;
    }
    .privacy-control {
      min-height: auto;
      font-size: 16px;
      line-height: 130%;
      padding-left: 0;
      cursor: pointer;
      .privacy-input {
        &:checked {
          & ~ .privacy-label {
            &::before {
              background-color: transparent;
            }
            &::after {
              background-position: center;
              background-position-x: 2px;
              background-repeat: no-repeat;
              transform-origin: center;
              animation: privacyBounce .5s;
              transform: scale(1);
              //background-image: url('data:image/svg+xml,<svg width="15" height="10" viewBox="0 0 15 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M13.3 1L5.37619 9L1 4.58173" stroke="#{$primary}" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
            }
          }
        }
      }
      .privacy-label {
        padding-left: 46px;
        cursor: pointer;
        &::after, &::before {
          width: 31px;
          height: 31px;
          border: 2px solid $primary;
          left: 0;
          top: -4px;
        }
        &::after {
          background: none;
          background-image: url('data:image/svg+xml,<svg width="15" height="10" viewBox="0 0 15 10" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M13.3 1L5.37619 9L1 4.58173" stroke="#{$primary}" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
          border-color: transparent;
          transform: scale(0);
        }
      }
    }
  }
  @media (max-width: 1689px) {
    .btn-box {
      .privacy-box {
        margin-left: 25px;
      }
      .privacy-control {
        font-size: 12px;
        .privacy-label {
          padding-left: 32px;
          &::after, &::before {
            width: 22px;
            height: 22px;
            left: 0;
            top: -4px;
          }
        }
      }
    }
  }
}

