.slider-nav-box {
  .slider-arrow-wrap, .owl-nav {
    display: flex;
    margin-left: -7px;
    margin-right: -7px;
    .slider-arrow-btn, & > button {
      margin-left: 7px;
      margin-right: 7px;
    }
  }
  .slider-arrow-btn, .owl-nav > button {
    @include center-box;
    width: 40px;
    height: 40px;
    border: 1px solid;
    border-radius: 50%;
    &.primary {
      background-color: $primary;
      border-color: $primary;
      color: $white;
    }
    &.white {
      background-color: $white;
      border-color: $white;
      color: $black;
    }
    &.disabled {
      opacity: .1;
    }
    &--next {}
    &--prev {}
    .icon {
      width: 14px;
    }
  }
}


.up-btn-box {
  width: 52px;
  height: 52px;
  position: fixed;
  right: 30px;
  bottom: 35px;
  visibility: hidden;
  opacity: 0;
  transform: scale(0);
  transition: all .5s;
  &.active {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
  }
  .up-btn {
    @include center-box;
    width: 100%;
    height: 100%;
    background-color: $primary;
    border: none;
    border-radius: 50%;
    color: $white;
    .icon {
      width: 100%;
      height: 100%;
    }
  }
}

.preloader-block {
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999;
  &.show {
    display: block;
  }
  &.loading {
    display: block;
    .preloader-box {
      .preloader-center-box {
        .loading-line {
          animation: loadingLine 5s;
        }
      }
    }
  }
  &.loaded {
    .preloader-box {
      opacity: 0;
      .preloader-center-box {
        .loading-line {
          width: 100%;
        }
      }
    }
  }
  .preloader-box {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $secondary;
    transition: opacity 1s;
    opacity: 1;
    .preloader-center-box {
      @include center-box;
      flex-grow: 1;
      position: relative;
      .loading-line {
        width: 0;
        height: 2px;
        background-color: $primary;
        position: absolute;
        left: 0;
        z-index: 5;
        transition: width .3s;
      }
      .logo-box {
        @include center-box;
        display: inline-flex;
        background-color: $secondary;
        padding: 0 60px;
        position: relative;
        z-index: 10;
      }
    }
    .loading-text {
      width: 100%;
      margin: 0;
      text-align: center;
      font-family: 'PF DinDisplay Pro', serif;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: $white;
      position: absolute;
      bottom: 56px;
    }
  }
}

@keyframes loadingLine {
  from {width: 0;}
  to {width: 100%;}
}
@keyframes privacyBounce {
  0% {transform: scale(0)}
  50% {transform: scale(1.2)}
  100% {transform: scale(1)}
}

