#fp-nav {
  &.light {
    color: #fff !important;
  }
  &.dark {
    color: #000 !important;
  }
  margin-top: -32px;
  color: #fff;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  position: fixed;
  top: 50%;
  z-index: 100;
  &.left {
    left: 20px;
  }
  &.right {
    right: 20px;
  }
  ul {
    margin: 0;
    padding: 0;
    li {
      width: 26px;
      height: 26px;
      display: block;
      margin: 3px 0;
      position: relative;
      a {
        width: 100%;
        height: 100%;
        display: block;
        color: currentColor;
        position: relative;
        z-index: 1;
        cursor: pointer;
        text-decoration: none;
        transition: all .3s;
        &::before {
          content: '';
          width: 100%;
          height: 100%;
          border: 2px solid transparent;
          border-radius: 50%;
          transform: rotate(45deg) scale(.5);
          position: absolute;
          top: 0;
          left: 0;
          transition: color .3s, transform .6s;
        }
        &.active {
          &::before {
            border-radius: 0;
            border-color: currentColor;
            transform: rotate(135deg) scale(1);
          }
        }
        span {
          width: 4px;
          height: 4px;
          background-color: currentColor;
          border: 0;
          border-radius: 50%;
          position: absolute;
          z-index: 1;
          left: 50%;
          top: 50%;
          margin: -2px 0 0 -2px;
          transition: all .3s;
        }
      }
    }
  }
}

